import { applyRefund } from '@/api'
export default {
	name: '',
	data () {
		return {
			desc: '',
			id: '', // 订单id
		}
	},
	onLoad (options) {
		this.id = options.id
	},
	methods: {
		async onSubmitClick () {
			if(this.desc == '') {
				uni.showToast({
					title: '输入退款原因,有助于加快退款进度哦',
					icon: 'none'
				})
				return;
			}
			let result = await applyRefund ({
				iid: this.id,
				refund: this.desc,
				token: uni.getStorageSync('token')
			})
			if(result.code == 200) {
				uni.showToast({
					title: '提交成功'
				})
				setTimeout(() => {
					uni.navigateBack()
				}, 1500)
			}
		}
	}
}
